<template>
    <v-app id="inspire">
        <v-navigation-drawer
            fixed
            v-model="drawer"
            app
            dark
            floating
            persistent
            mobile-break-point="991"
            width="260"
            :style="{'background-image': `url(${sideimage})`, 'background-position': 'center center', 'background-size': 'cover'}"
        >
            <v-list dense style="background: rgba(27,27,27,.74);height:100%;overflow:auto;">
                <v-list-tile avatar style="padding: 15px 0;" exact to="/blog">
                    <v-list-tile-avatar color="white">
                        <v-img :src="authorimage" height="34" contain/>
                    </v-list-tile-avatar>
                    <v-list-tile-title class="title">profos</v-list-tile-title>
                </v-list-tile>
                <v-divider/>
                <template v-for="item in items">
                    <v-list-group
                        v-if="item.children"
                        v-model="item.model"
                        :key="item.text"
                    >
                        <v-list-tile slot="activator">
                            <v-list-tile-content>
                                <v-list-tile-title>{{ item.text }}</v-list-tile-title>
                            </v-list-tile-content>
                        </v-list-tile>
                        <v-list-tile active-class="grey lighten-1" exact :to="child.url" v-for="(child, i) in item.children" :key="i">
                            <v-list-tile-content>
                                <v-list-tile-title style="padding-left: 15px;">{{ child.text }}</v-list-tile-title>
                            </v-list-tile-content>
                        </v-list-tile>
                    </v-list-group>
                    <v-list-tile exact :to="item.url" v-else :key="item.text">
                        <v-list-tile-content>
                            <v-list-tile-title>{{ item.text }}</v-list-tile-title>
                        </v-list-tile-content>
                    </v-list-tile>
                </template>
            </v-list>
        </v-navigation-drawer>
        <v-toolbar app flat prominent style="background: #eee;">
            <v-toolbar-title style="width: 300px" class="ml-0 pl-3">
                <v-toolbar-side-icon @click.stop="drawer = !drawer"></v-toolbar-side-icon>
            </v-toolbar-title>
            <v-spacer></v-spacer>
            <v-btn flat fab dark small color="primary" @click="isShow=!isShow">
                <v-icon dark v-if="isShow">keyboard_arrow_right</v-icon>
                <v-icon dark v-else>keyboard_arrow_left</v-icon>
            </v-btn>
            <iframe v-show="isShow" class="animate-star" src="https://ghbtns.com/github-btn.html?user=qzruncode&repo=Front-end-of-actual-combat&type=star&count=true&size=large" frameborder="0" scrolling="0" width="160px" height="30px"></iframe>
            
        </v-toolbar>
        <v-content style="background: #eee;">
            <v-container fluid fill-height style="position:relative;">
                <v-layout justify-center align-center>
                    <transition name="fade" mode="out-in">
                        <router-view></router-view>
                    </transition>
                </v-layout>
            </v-container>
        </v-content>
        <v-footer class="pa-3">
            <v-spacer></v-spacer>
            <div>Copyright &copy; {{ new Date().getFullYear() }} </div>
        </v-footer>
    </v-app>
</template>

